module.exports = "import Ratio from 'react-bootstrap/Ratio';\n\nfunction DefaultExample() {\n  return (\n    <>\n      {['1x1', '4x3', '16x9', '21x9'].map((ratio) => (\n        <Ratio aspectRatio={ratio}>\n          <div>{ratio}</div>\n        </Ratio>\n      ))}\n    </>\n  );\n}\n\nexport default DefaultExample;\n"